.intro {
    width: 100%;
    display: flex;
	flex-direction: column;
    justify-content: center;
    color: $white;
    &__content {
        width: 100%;
        padding: 200px 0 100px;
        max-width: 700px;
        margin: 0 auto;
        font-family: $gordita-medium;
        p {
            font-size: .7rem;
            line-height: 1.2rem;
            margin: 5px 100px;
        }
    }
    &__title {
        font-size: 2rem;
        color: $burgundy;
        &:nth-child(2) {
            padding: 5px 42px 10px;
            color: $white;
            font-size: 1.5rem;
        }
    }
    .global__button {
        margin: 20px 20px 20px 0;
        font-size: .8rem;
    }
}
