html {
    scroll-behavior: smooth;
}

* {
    outline: none;
}

:root {
    font-size: 20px;
}

body {
    width: 100%;
    height: 100%;
    // background-color: $lightblack;
    background-color: #000000e6;
    font-size: 20px;
}

a {
    text-decoration: none;
    color: $white;
}

ul {
    list-style: none;
    padding: 0;
}


.container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
}


.global__button {
    position: relative;
    display: inline-block;
    font-family: $gordita-regular;
    font-size: 1.1rem;
    color: $white;
    transition: color .1s linear;
    text-transform: uppercase;
    opacity: .8;
    &:hover {
        opacity: 1;
    }
}

.work__image--reverse-right {
    float: right !important;
}

.work__image--reverse-left {
    float: left !important;
}

.transition-fade {
    transition: .4s;
    opacity: 1;
}

html.is-animating .transition-fade {
    opacity: 0;
}

.clear {
    clear: both;
}